<script>
export default {
  props: ["component"],

  methods: {
    getAncestors() {
      var ancestors = new Array();

      ancestors.push(this);

      let parent = this.$parent;
      while (parent != null) {
        ancestors.push(parent);
        parent = parent.$parent;
      }

      return ancestors;
    },

    getChildren() {
      // this.$
    },

    extractIdentifiableComponentById(comp, id) {
      console.log(comp);
      return null;
    },

    getIdentifiableComponentById(id) {
      let comp = null;

      if (!id) {
        return comp;
      }

      const ancestors = getAncestors();

      if (id === "webup") {
        // TODO return webup component
        return comp;
      }

      for (parent in ancestors) {
        comp = extractIdentifiableComponentById(parent, id);
        if (comp != null) {
          break;
        }
      }

      return comp;
    },

    sendDynamism(dynamism) {
      console.log("sending dynamism: ", dynamism);
    }
  }
};
</script>
